.catalog__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0 0 25px;
  padding: 0;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    margin-bottom: 69px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 76px;
    row-gap: 63px;
    margin-bottom: 78px;
  }
}
